body {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    margin: 0 auto;
}

.navbar {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    font-size: 20px;
    background-color: #343434;
    padding: 10px;
}

.nav-links {
    display: -ms-flexbox;
    display: flex;
}

.logo {
    font-family: 'Dancing Script', cursive;
    color: #ffffff;
    margin-left: 30px;
    font-size: 20px;
}

.link {
    margin: 0 15px;
    text-decoration: none;
    color: #919191;
    outline: none;
    -webkit-transition: color 0.5s ease;
    -o-transition: color 0.5s ease;
    transition: color 0.5s ease;
}

.link:hover {
    color: #ffffff;
    -webkit-transform: scale(1.3);
        -ms-transform: scale(1.3);
            transform: scale(1.3);
}

.project-list {
    overflow-x: scroll;
    overflow-y: hidden;
    white-space: nowrap;
}

.project-elem {
    display: inline-block;
    margin: 15px;
    cursor: pointer;
}
  
.project-item-wrapper {
    display: block;
    text-align: center;
}
  
.project-item-img {
    border: 3px solid #000000;
}
  
.project-item-title {
    font-size: 15px;
    color: #000000;
}


